import React, {Component} from 'react';
import './projects.scss';
import {Container} from 'react-bootstrap';
import ProjectContainer from './projectContainer';
import Prismic from 'prismic-javascript'
import { RichText } from 'prismic-reactjs'

const apiEndpoint = 'https://eli-portfolio.cdn.prismic.io/api/v2';

class Projects extends Component {
    

    static async getInitialProps(context) {
        const req = context.req
        const home = await this.getHomePage(req)
        return {
          doc: home
        }
      }
     
      static async getHomePage (req) {
        const API = await Prismic.getApi(apiEndpoint, { req })
        return await API.getSingle('homepage')
      }


    render() {
        // console.log(RichText);
        
        return (
            <Container>
                <div className="projects-section">
                    <div className="projects-title">
                        <h2>PROJECTS</h2>
                    </div>
                    <div className="projects">
                        <ProjectContainer alt="left" />
                        <ProjectContainer alt="right" />
                        <ProjectContainer alt="left" />
                        <ProjectContainer alt="right" />
                    </div>
                </div>
            </Container>
        )
    }
}

export default Projects